*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --app-padding-inline: 1.5rem;
  --app-padding-block: 3rem;
  --font-size-s: 1rem;
  --font-size-m: 2rem;
  --font-size-l: 3rem;
  --icon-width-s: 16px;
  --icon-width-normal: 32px;
  --icon-width-m: 48px;
  --icon-width-l: 64px;
}

body {
  width: 100%;
  min-height: 100vh;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

#app {
  width: 100%;
  min-height: 100vh;
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position-y: center;
  color: white;
  padding: var(--app-padding-inline) var(--app-padding-block);
}

.main {
  display: flex;
  justify-content: space-between;
  overflow: hidden;
}

/* Weather Info */
.weather-info {
}

.weather-info__description {
  font-size: var(--font-size-m);
  margin-bottom: 0.2rem;
}

.weather-info__city {
  font-size: var(--font-size-s);
  margin-bottom: 0.4rem;
}

.weather-info__date,
.weather-info__time {
  font-size: calc(var(--font-size-s) - 0.15rem);
}

.weather-info__time {
  margin-bottom: 1rem;
}

.weather-info__temperature {
  font-size: var(--font-size-l);
  margin-block: 1rem;
}

.weather-info__icon {
  margin-block: 1rem;
}

.search-box {
  display: inline-block;
  margin-bottom: 3rem;
  position: relative;
}

.search-box > input {
  font-size: var(--font-size-s);
  border: none;
  border-bottom: 2px solid white;
  outline: none;
  background-color: transparent;
  padding: 0.3em 0.8em;
  color: white;
}
.search-box > input::-webkit-input-placeholder {
  color: white;
}

.search-box > button {
  outline: none;
  border: none;
  background-color: transparent;
  position: absolute;
  font-size: calc(var(--font-size-s) + 0.5rem);
  cursor: pointer;
  top: -4px;
  right: 0;
}

.error-message {
  position: absolute;
  font-family: Arial, Helvetica, sans-serif;
  left: 0;
  bottom: 0;
  transform: translateY(calc(100% + 0.5rem));
  font-size: calc(var(--font-size-s) - 0.15rem);
  font-style: italic;
}

/* Weather details container  */
.weather-details-container {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.weather-details {
  display: flex;
  align-items: center;
  gap: 0.8rem;
}

.weather-details__icon {
  align-self: flex-start;
}

.weather-details__icon > img {
  width: var(--icon-width-normal);
  height: var(--icon-width-normal);
}

.weather-details__label {
  font-size: var(--font-size-s);
  margin-bottom: 0.2em;
}

.weather-details__data {
  font-size: var(--font-size-m);
}

/* Buttons change forecast */
.change-forecast {
  margin-bottom: 1.5rem;
  display: flex;
  align-items: center;
  gap: 1rem;
}

/*  Hourly and Daily*/
.forecast-hourly-container,
.forecast-daily-container {
  display: none;
}

.forecast-hourly-container.active,
.forecast-daily-container.active {
  display: flex;
  overflow: hidden;
}

/* Hourly */
.forecast-hourly {
  min-width: calc(100% / 8);
  max-width: calc(100% / 8);
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-inline: auto;
}

/* Daily */
.forecast-daily {
  min-width: calc(100% / 7);
  max-width: calc(100% / 7);
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.forecast-daily__day {
  font-size: calc(var(--font-size-s) + 0.3rem);
}

.forecast-daily__temperature-hight {
  font-size: var(--font-size-m);
  margin-bottom: 2px;
}

.forecast-daily__temperature-low {
  font-size: var(--font-size-s);
}

.forecast-daily__icon {
  display: inline-block;
}

/* Navigation */
.nav {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  display: none;
}

.nav.active {
  display: flex;
}

.nav > button {
  outline: none;
  border: none;
  background-color: transparent;
  cursor: pointer;
}

.nav > button > img {
  width: var(--icon-width-normal);
  height: var(--icon-width-normal);
}
.dots-container {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}
.dots-container > div {
  width: 9px;
  height: 9px;
  border-radius: 50%;
  border: 1px solid white;
  background-color: transparent;
  cursor: pointer;
}

.dots-container > div.active {
  background-color: white;
}

/* Buttons */
.btn {
  border: 2px solid white;
  outline: none;
  font-size: 1rem;
  padding: 0.4em 1em;
  border-radius: 0.2em;
  background-color: transparent;
  color: white;
  cursor: pointer;
  transition: background 500ms ease-in-out;
}

.btn.active,
.btn:active,
.btn:hover {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

/*  */
.loading {
  position: fixed;
  inset: 0;
  background-color: rgba(0, 0, 0, 0.3);
  display: none;
  justify-content: center;
  align-items: center;
}

@media only screen and (max-width: 1200px) {
  :root {
    --app-padding-inline: 1rem;
    --app-padding-block: 2rem;
    --font-size-s: 1rem;
    --font-size-m: 1.5rem;
    --font-size-l: 2.5rem;
  }
}

@media only screen and (max-width: 1000px) {
  .nav.active {
    display: none;
  }

  .forecast-daily-container,
  .forecast-hourly-container {
    flex-wrap: wrap;
    gap: 1rem;
  }

  .forecast-daily,
  .forecast-hourly {
    min-width: calc(33.33% - 1rem);
    max-width: calc(33.33% - 1rem);
    padding: 1rem;
    border: 1px solid rgba(255, 255, 255, 0.3);
    border-radius: 0.2rem;
    justify-content: center;
  }
}

@media only screen and (max-width: 500px) {
  #app {
    display: flex;
    flex-direction: column;
    gap: 3rem;
  }

  :root {
    --app-padding-inline: 1rem;
    --app-padding-block: 2rem;
    --font-size-s: 0.85rem;
  }

  .main {
    flex-direction: column;
  }

  .change-forecast {
    justify-content: center;
  }

  .forecast-daily,
  .forecast-hourly {
    min-width: calc(50% - 1rem);
    max-width: calc(50% - 1rem);
    padding: 1rem;
    border: 1px solid rgba(255, 255, 255, 0.3);
    border-radius: 0.2rem;
    justify-content: center;
  }
}

